import { Elements } from "@stripe/react-stripe-js";
import CheckoutPage from "./CheckoutPage";
import { loadStripe } from "@stripe/stripe-js";
import { useAppDispatch } from "../../app/store/configureStore";
import { useEffect, useState } from "react";
import agent from "../../app/api/agent";
import { Basket } from "../../app/model/basket";
import { setBasket } from "../basket/basketSlice";
import Loading from "../../app/layout/Loading";

const stripePromise = loadStripe('pk_test_51N31ewBGfQQXi3tIxNGQKZ1RiFHc67p6ABeHDKJQveoRSCyQHfufPv1wPTQj5vU4FwbV7wthwE32nnlIJ29Aq9Dj002YOHCgdu');

const CheckoutWrapper = () => {

    const dispatch = useAppDispatch();
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        agent.Payments.createPaymentIntent()
            .then((res: Basket) => dispatch(setBasket(res)))
            .catch(error => console.log(error))
            .finally(() => setLoading(false));
    }, [dispatch]);

    if (loading) return <Loading message="Loading checkout ..." />

    return (
        <Elements stripe={stripePromise}>
            <CheckoutPage />
        </Elements>
    );
}

export default CheckoutWrapper;