import { FormGroup, FormControlLabel, Checkbox } from "@mui/material";
import { useState } from "react";

interface Props {
    items: string[];
    checked?: string[];
    onChange: (items: string[]) => void;
}
const CheckboxButtons = ({ items, checked, onChange }: Props) => {
    const [checkItems, setChekckedItems] = useState(checked || []);

    function handleChecked(value: string) {
        const currentIndex = checkItems.findIndex(item => item === value);
        let newChecked: string[] = [];
        if (currentIndex === - 1)
            newChecked = [...checkItems, value];
        else
            newChecked = checkItems.filter(item => item !== value);
        setChekckedItems(newChecked);
        onChange(newChecked);
    }

    return (
        <FormGroup>
            {items.map((item) => (
                <FormControlLabel
                    control={<Checkbox
                        checked={checkItems.indexOf(item) !== -1}
                        onClick={() => handleChecked(item)}
                    />}
                    label={item}
                    key={item} />
            ))}
        </FormGroup>
    );
}

export default CheckboxButtons;