import { Typography, Grid } from "@mui/material";
import { useFormContext } from "react-hook-form";
import AppTextInput from "../../app/components/AppTextInput";
import AppCheckbox from "../../app/components/AppCheckbox";


const AddressForm = () => {
    const { control, formState } = useFormContext()
    return (
        <>
            <Typography variant="h6" gutterBottom>
                Shipping address
            </Typography>
            <Grid container spacing={3}>
                <Grid item xs={12} sm={12}>
                    <AppTextInput control={control} label="Full Name" name="fullName" type="text" />
                </Grid>
                <Grid item xs={12} sm={2}>
                    <AppTextInput control={control} label="Zipcode" name="zipcode" type="text" />
                </Grid>
                <Grid item xs={12} sm={4}>
                    <AppTextInput control={control} label="Street" name="street" type="text" />
                </Grid>
                <Grid item xs={12} sm={4}>
                    <AppTextInput control={control} label="Neighborhood" name="neighborhood" type="text" />
                </Grid>
                <Grid item xs={12} sm={2}>
                    <AppTextInput control={control} label="Number" name="number" type="number" />
                </Grid>
                <Grid item xs={12} sm={3}>
                    <AppTextInput control={control} label="City" name="city" type="text" />
                </Grid>
                <Grid item xs={12} sm={3}>
                    <AppTextInput control={control} label="State/Province/Region" name="state" type="text" />
                </Grid>
                <Grid item xs={12} sm={3}>
                    <AppTextInput control={control} label="Country" name="country" type="text" />
                </Grid>
                <Grid item xs={12} sm={3}>
                    <AppTextInput control={control} label="Complement" name="complement" type="text" />
                </Grid>
                <Grid item xs={12}>
                    <AppCheckbox
                        disable={!formState.isDirty}
                        name="saveAddress"
                        label="Save this as the default address"
                        control={control} />
                </Grid>
            </Grid>
        </>
    );
}

export default AddressForm;